.signing-container .fb-button,
.signing-container .google-button {
  margin-top: 10px;
  border: none;
  background: transparent;
  width: 100%;
  height: 72px;
  border-radius: 36px;
  display: flex;
  justify-content: center;
  align-items: center; }
  .signing-container .fb-button .signing-inner,
  .signing-container .google-button .signing-inner {
    margin: auto;
    display: flex; }
    .signing-container .fb-button .signing-inner img,
    .signing-container .google-button .signing-inner img {
      transform: translateY(3px);
      height: 20px;
      width: 20px;
      margin-right: 16px;
      margin-left: auto; }
    .signing-container .fb-button .signing-inner p,
    .signing-container .google-button .signing-inner p {
      color: white;
      margin: 0; }
  .signing-container .fb-button:focus, .signing-container .fb-button:active,
  .signing-container .google-button:focus,
  .signing-container .google-button:active {
    outline: none; }

.signing-container .fb-button {
  background-color: #3b5998; }
  .signing-container .fb-button:hover {
    background-color: #15264b;
    text-decoration: none; }
  .signing-container .fb-button:active {
    background-color: #527acf; }

.signing-container .google-button {
  background-color: #dd4b39; }
  .signing-container .google-button:hover {
    background-color: #80281c;
    text-decoration: none; }
  .signing-container .google-button:active {
    background-color: #fd5943; }

.signing-container .error-msg {
  color: #ff6779;
  font-size: 14px;
  font-weight: 600; }
