.login-tab-wrapper {
  max-width: 400px;
  width: 100%;
  margin: 0 auto auto auto; }

.login-container {
  margin-top: 10px;
  max-width: 400px;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center; }
  .login-container p,
  .login-container span {
    color: #46737A;
    font-size: 18px;
    font-weight: normal; }
  .login-container .col-12 {
    max-width: 345px; }
  .login-container .welcome-text {
    font-size: 24px;
    font-weight: bold;
    color: #354F53; }
  .login-container .seperator-less-margin {
    margin-top: -10px !important; }
  .login-container .seperator {
    margin-top: 10px;
    width: 100%;
    display: flex;
    justify-content: center; }
    .login-container .seperator p {
      color: #C8E0E4;
      position: absolute;
      top: 3px;
      background-color: white;
      width: 120px; }
    .login-container .seperator hr {
      width: 100%;
      height: 1px;
      background-color: #C8E0E4;
      border: none; }
  .login-container .forget-pass {
    margin-top: -5px;
    margin-bottom: 10px;
    display: flex;
    justify-content: flex-end; }
    .login-container .forget-pass p {
      font-size: 16px;
      font-weight: normal; }
  .login-container .facebook {
    color: #3b5998;
    font-weight: bold; }
  .login-container .google {
    color: #dd4b39;
    font-weight: bold; }
  .login-container .default {
    color: #46737A; }
  .login-container .caption {
    font-size: 13px;
    font-weight: normal; }
  .login-container .no-account {
    font-size: 18px;
    font-weight: normal; }
  .login-container .sign-up {
    font-size: 18px;
    font-weight: 600; }
  .login-container .link-color {
    color: #00D7AF; }
  .login-container .link-bold {
    color: #00D7AF;
    font-weight: 600; }
  .login-container .link-color,
  .login-container .link-bold {
    transition: color 0.3s ease; }
    .login-container .link-color:hover,
    .login-container .link-bold:hover {
      color: #36B29B;
      cursor: pointer;
      text-decoration: underline; }
  .login-container .errorString {
    color: #ff6779; }
  .login-container .terms-container {
    text-align: left;
    margin-top: 17px;
    margin-bottom: 19px; }
  .login-container form {
    width: 100%; }
    .login-container form .col-12 {
      padding: 0; }
    .login-container form .button-medium-color {
      min-width: 100% !important; }
      .login-container form .button-medium-color p {
        color: white; }

.login-modal {
  max-width: 516px; }
  .login-modal .modal-header {
    padding-top: 10px;
    padding-bottom: 0; }
  .login-modal .modal-body {
    padding-top: 0; }
